.form {
  width: 100%;

  &__input {
    box-sizing: border-box;
    width: 100%;
    height: 48px;
    background-color: #F9F9F9;
    color: #7E7E83;
    border-radius: 8px;
    border: none;

    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.15px;
    margin-bottom: 16px;
    padding: 14px 16px;
    resize: none;

    &:focus  {
      border: none;
      outline: 2px solid #F98921;
    }
  }

  &__button {
    width: 100%;
    height: 56px;
    padding: 0 48px;
    border-radius: 8px;
    background: $main-text-color;
    color: #FFF;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    border: none;
    margin-top: 32px;

    transition: background-color 0.3s ease-out;

    @include hover(background-color, #F98921);
  }

}


