.icon {
  width: 24px;
  height: 24px;
  display: block;
  cursor: pointer;
  text-decoration: none;
  background-size: cover;
  transition: transform 0.3s;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    width: 32px;
    height: 32px;
  }

  &__phone-container {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  &--phone {
    background-image: url(../images/icons/phone-icon.png);
  }

  &--menu {
    background-image: url(../images/icons/burger-menu-icon.png);

    &:hover {
      background-image: url(../images/icons/menu-hover-icon.png);
    }

  }

  &--close {
    background-image: url(../images/icons/close-icon.png);
  }



  @include on-desktop {
    &--phone {
      background-image: url(../images/icons/phone-icon.png);
      &::before {
        width: 151px;
        content: '+1 234 5555-55-55';
        display: block;
        font-family: Inter, sans-serif;
        color: $main-text-color;
        font-size: 12px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;

        padding: 9px 9px 9px 0;
        transition: all 0.3s;
        transform: translateX(-85%);
        opacity: 0;
      }

      &:hover::before {
        transform: translateX(-100%);
        opacity: 1;
      }
    }
  }

}


