.product {
  &__link {
    height: 100%;
    text-decoration: none;
    color: $main-text-color;
  }

  &__img {
    width: 100%;
    max-width: 280px;
    transition: transform 0.3s;

    @include hover(transform, scale(1.1));

    @include on-tablet {
      max-width: 600px;
    }

    @include on-desktop {
      max-width: 324px;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    margin: 24px 0 8px;
    text-transform: uppercase;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.15px;
    margin: 0;
  }

  &__price {
    font-weight: 700;
    margin: 12px 0 0;
  }

  @include on-tablet {
    &__price {
      margin: 34px 0 0;
    }

    &__item {
      margin-bottom: 56px;
    }
  }
}
