.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #F9F9F9;
  font-weight: 700;
  text-transform: uppercase;

  &__nav {
    margin-bottom: 40px;
  }

  &__top {
    margin-bottom: 32px;
    display: flex;
    justify-content: flex-end;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__phone-number {
    margin-bottom: 16px;
    display: block;
    color: inherit;
    text-decoration: none;
    letter-spacing: 2px;
    font-size: 16px;
  }

  &__call-to-order {
    display: block;
    color: inherit;
    text-decoration: none;
    letter-spacing: 2px;
    font-size: 16px;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;
    width: fit-content;
  }

  @include on-tablet {
    &__top {
      margin-bottom: 0;
    }
  }
}
