.gallery {
  box-sizing: border-box;
  list-style: none;
  padding: 0;
  margin: 0;

  &__item {
    @include page-grid;
  }

  &__link {
    text-decoration: none;
    transition: transform .2s linear;
    &:first-child {
      grid-column: 1;
    }

    &:last-child {
      grid-column: 2;
    }

    @include hover(transform, scale(1.1));

  }

  &__image {
    width: 100%;
    height: 100%;
    background-size: cover;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    margin: 16px 0 40px;
  }

  @include on-tablet {
    &__image {
      max-width: 288px;
      max-height: 184px;
    }

    &__link {
      &:first-child {
        grid-column: 1 / 3;
      }

      &:last-child {
        grid-column: 4 / 6;
      }
    }

    &__title {
      font-size: 24px;
      margin: 16px 0 56px;
    }
  }

  @include on-desktop {
    &__image {
      max-width: 498px;
      max-height: 324px;
    }

    &__link {
      &:first-child {
        grid-column: 1 / 6;
      }

      &:last-child {
        grid-column: 7 / 12;
      }
    }

    &__title {
      max-width: 498px;
      font-size: 18px;
      font-weight: 700;
    }

    &__text {
      max-width: 411px;
    }
  }
}

.view-all-btn {
  border-radius: 8px;
  background: #F98921;
  color: #FFF;
  border: none;

  width: 100%;
  height: 56px;
  padding: 0 48px;

  font-size: 16px;
  line-height: 27px;
  font-weight: 700;

  transition: background-color 0.3s ease-out;

  @include hover(background-color, $main-text-color);

  &:active {
    background-color: #010810;
  }

  @include on-desktop {
    width: 324px;
    height: 56px;
  }
}

