
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 24px;

  @include on-tablet {
    padding-block: 64px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    width: 152px;
    height: 16px;
    position: relative;
    z-index: 2;
    transition: transform 0.3s;

    @include on-tablet {
      width: 228px;
      height: 24px;
    }

    @include hover(transform, scale(1.2));


    &::before {
      content: '';
    display: block;
    position: absolute;

    height: 100%;
    width: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    transition: width 0.5s linear;
    background-color: rgb(194, 194, 194);
    }
  }
}

.top-bar__logo:hover::before {
  width: 100%;
}


