.page {
  font-family: Manrope, sans-serif;
  font-family: Inter, sans-serif;
  color: $main-text-color;
  font-size: 16px;
  line-height: 140%;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;

    &:has(.page__menu:target) {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0%);
    }
  }
}
