.about-us {
  box-sizing: border-box;
  width: 100%;
  background-color: #FCFAEF;

  &__image {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    background-image: url(../images/about-us-bg.png);
  }

  &__content {
    @include page-grid;

    padding: 48px 0;
    color: inherit;
    box-sizing: border-box;
    gap: 0;
  }

  &__title {
    margin: 0 0 24px;
    font-size: 32px;
    line-height: 32px;
    grid-column: 1 / -1;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.15px;
    margin: 0;
    grid-column: 1 / -1;
  }

  @include on-tablet {
    &__image {
      height: 400px;
    }

    &__content {
      gap: 24px;
      padding: 120px 0;
    }

    &__title {
      font-size: 40px;
      line-height: 100%;
      margin: 0;
      grid-column: 1 / 3;
    }

    &__text {
      grid-column: 3 / 6;
    }
  }

  @include on-desktop {
    &__content {
      gap: 111px;
    }

    &__title {
      grid-column: 1 / 6;
      font-size: 48px;
      line-height: 48px;
    }

    &__text {
      grid-column: 6 / 12;
    }
  }
}
