.nav {
  &__link {
    text-decoration: none;
    line-height: 22px;
    font-size: 22px;
    font-weight: 700;
    color: $main-text-color;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      transform: scaleX(0);
      height: 1px;
      bottom: -4px;
      left: 0;
      background-color: $main-text-color;

      transition: transform 0.3s;
    }

    &:hover::after {
      transform: scaleX(1);
      transform-origin: left
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__item {
    position: relative;
  }
}


