.footer {
  padding: 64px 0;

  @include on-tablet {
    padding: 120px 0;
  }

  &__content {
    @include page-grid;
  }

  &__form {
    grid-column: 1 / -1;
  }

  &__contacts {
    grid-column: 1 / -1;
  }

  @include on-desktop {
    &__content {
      gap: 111px;
    }

    &__form {
      grid-column: 1 / 6;
    }

    &__contacts {
      grid-column: 6 / 12;
    }
  }
}
