.contacts {
  font-family: Manrope, sans-serif;
  margin-top: 48px;

  &__contact {
    margin: 0;
    transition: transform .2s linear;
    transform-origin: left top;

    @include hover(transform, scale(1.1));
  }

  &__title {
    color: #9393A3;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.15px;
  }

  &__data {
    text-decoration: none;
    color: #191919;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.15px;
    margin: 8px 0 24px;

    &:first-child {
      margin-top: 0;
    }
  }
}
