@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-in-line() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1020px;
    padding-inline: 120px;
    margin-inline: auto;
  }
}

@mixin hover ($property, $toValue) {

  transition: #{$property} 0.3s;

  &:hover {
    #{$property}:$toValue;
  }
}

.container {
  @include content-padding-in-line;
}

@mixin page-grid {
  display: grid;
  gap: 20px;

  --colums: 2;

  grid-template-columns: var(--colums), 1fr;

  @include on-tablet {
    gap: 24px;

    --colums: 6;
  }

  @include on-desktop {
    --colums: 12;
  }
}
